.App {
    min-width: 100vw;
    height: 100vh;
    background-image: url("/src/background.jpg");
    background-size: cover;
    /* background-position: cover; */
    background-repeat: no-repeat;
    color: white;
}

.App-logo {
    height: 15vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

ul li,
header,
footer {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

header {
    min-width: 100vw;
    min-height: 20vh;
    background-color: rgb(34, 25, 25);
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    gap: 20px;
}

header p {
    font-size: 2.5em;
    font-weight: bolder;
}

.myspace {
    width: 50vw;
    min-height: 60vh;
    margin: 0 auto;
}

.head {
    font-size: 3em;
    font-weight: bolder;
}

ul li a {
    color: white;
    font-size: 1.5em;
}

.App-footer {
    min-width: 100vw;
    min-height: 10vh;
    background-color: rgb(34, 25, 25);
    text-align: right;
}

.App-footer p {
    padding: 20px 20px 0 0;
    font-weight: bolder;
}